@use "global" as *;

.blog-archive__inner {
  margin-left: rem(25);
  margin-right: rem(25);
  @include mq("md") {
    margin-left: 0;
    margin-right: 0;
  }
}
.blog-archive__link {
  margin-top: rem(35);
  color: $green1;
  font-size: rem(24);
}
.blog-archive__link a:hover {
  opacity: 0.7;
}
.blog-archive__year {
  color: $green1;
}
.blog-archive__year span {
  transform: rotate(90deg);
}
