@use "global" as *;

.blog-contents-main {
  width: 100%;
}

.blog-contents-main__title {
  font-size: rem(24);
  border-bottom: 1px solid $green1;
  padding-bottom: rem(5);
  @include mq("md") {
    font-size: rem(32);
  }
}
.blog-contents-main__body {
  margin-top: rem(25);
}
.blog-contents-main__body p {
  margin-top: rem(30);
  @include mq("md") {
    margin-top: rem(50);
  }
}
.blog-contents-main__body img:nth-child(n + 1) {
  margin-top: rem(20);
  @include mq("md") {
    margin-top: rem(25);
  }
}
.blog-contents-main__body ul {
  margin-top: rem(20);
  font-size: rem(16);
  @include mq("md") {
    margin-top: rem(40);
  }
}
.blog-contents-main__wp-pagenavi {
  margin-top: rem(40);
  width: rem(158);
  margin-left: auto;
  margin-right: auto;
  @include mq("md") {
    margin-top: rem(80);
  }
}
