@use "global" as *;

.inner.terms-of-service__inner {
  position: relative;
  width: 100%;
  max-width: rem(600);
  @include mq("md") {
    max-width: rem(850);
  }
}
.terms-of-service__inner::before {
  content: "";
  position: absolute;
  width: rem(120);
  height: rem(55);
  top: rem(-25);
  left: calc(50% - min(48.5vw, rem(620)));
  background-image: url(../images/common/top-voice-fish2.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  @include mq("md") {
    width: rem(301);
    max-width: rem(1280);
    height: rem(138);
    top: rem(-70);
  }
}
.terms-of-service__contents {
  text-align: left;
}
.terms-of-service__title {
  font-size: rem(24);
  font-weight: $medium;
  @include mq("md") {
    font-size: rem(32);
  }
}
.terms-of-service__text ol li {
  list-style: decimal;
}
.terms-of-service__text {
  margin-top: rem(40);
  font-weight: $regular;
}
.terms-of-service__text1 {
  line-height: 2;
}
.terms-of-service__text2 {
  margin-top: rem(30);
  margin-left: rem(5);
  line-height: 2;
  padding-left: 1em;
}
.terms-of-service__text3 {
  margin-top: rem(35);
  line-height: 2;
}
.terms-of-service__text4 {
  margin-left: rem(5);
  line-height: 2;
  padding-left: 1em;
}
.terms-of-service__text5 {
  line-height: 2;
}
.terms-of-service__text6 {
  margin-top: rem(30);
  margin-left: rem(5);
  line-height: 2;
  padding-left: 1em;
  @include mq("md") {
    margin-top: rem(30);
  }
}
.terms-of-service__text7 {
  margin-top: rem(30);
  line-height: 2;
}
.terms-of-service__text8 {
  margin-left: rem(5);
  line-height: 2;
  padding-left: 1em;
  letter-spacing: 0.005em;
}
.terms-of-service__text9 {
  margin-top: rem(30);
  line-height: 2;
}
